import socketIOClient from "socket.io-client";
import urls from "./urls";

const socket = socketIOClient(urls.socket_url);
socket.on("connect", () => {
  console.log("I connected...", new Date().toLocaleTimeString());
});
socket.on("disconnect", () => {
  console.log("I disconnected...", new Date().toLocaleTimeString());
});
socket.on("drivers", (drivers) => {
  console.log(
    "drivers available are...",
    drivers,
    new Date().toLocaleTimeString()
  );
});

export default socket;
