import React from "react";
import Box from "@material-ui/core/Box";
import TabTheme from "../components/core/TabTheme";
import Typography from "@material-ui/core/Typography";
import MyVirtualTable from "../components/core/VirtualTableTheme";

import { DataTypeProvider } from "@devexpress/dx-react-grid";
import Axios from "axios";
import NotificationsContext from "../components/context/notifications/NotificationsContext";
import {
  Button,
  DialogContent,
  DialogActions,
  DialogTitle,
  Dialog,
  TextField,
  Grid,
  Select,
  FormControl,
  MenuItem,
  FormControlLabel,
  InputLabel,
  Paper,
  Divider,
} from "@material-ui/core";
import GoogleMaps from "../components/form/SearchPlaces";
import SearchUser from "../components/form/SearchUsers";
import DriversContext from "../components/context/drivers/DriversContext";
import priceFormatter from "../components/core/priceFormatter";
import AlertContext from "../components/context/alert/alertContext";

const rows = [];
for (let index = 0; index < 100; index++) {
  rows.push({
    id: index,
    dateCreated: new Date().toISOString(),
    client: "Samuel Seibidor",
    pickUp: "10, Wogu Street D/Line, Port Harcourt",
    dropOff: "Creekside Logistics, Port Harcourt",
    distance: 5,
    mode: "Motorcycle",
  });
}

const columns = [
  { name: "dateCreated", title: "Date Created" },
  { name: "client", title: "Client" },
  { name: "pickUp", title: "Pick-up" },
  { name: "dropOff", title: "Drop-off" },
  { name: "distance", title: "Distance (km)" },
  { name: "mode", title: "Mode" },
];

const columnExtensions = [
  { columnName: "distance", width: 120, align: "center" },
];

const DateFormatter = ({ value }) => new Date(value).toLocaleDateString();

const DateFormatProvider = (props) => (
  <DataTypeProvider formatterComponent={DateFormatter} {...props} />
);

const NewDelivery = (props) => {
  const [open, setOpen] = React.useState(false);
  const { modes } = React.useContext(DriversContext);
  const { setAlert } = React.useContext(AlertContext);
  const [from, setFrom] = React.useState(null);
  const [to, setTo] = React.useState(null);
  const [pickUpNumber, setPickUpNumber] = React.useState("");
  const [dropOffNumber, setDropOffNumber] = React.useState("");
  const [note, setNote] = React.useState("");
  const [mode, setMode] = React.useState(modes[0] || "");
  const [distance, setDistance] = React.useState(null);
  const [price, setPrice] = React.useState(null);
  const [client, setClient] = React.useState(null);

  const [loading, setLoading] = React.useState(false);

  const cleanupMini = () => {
    setDistance(null);
    setPrice(null);
  };

  const close = () => {
    setOpen(false);
    setFrom(null);
    setTo(null);
    cleanupMini();
    setNote("");
    setPickUpNumber("");
    setDropOffNumber("");
    setMode(modes[0] || "");
  };

  React.useEffect(() => {
    let mounted = true;
    if (from && to && mode) {
      (async () => {
        try {
          setLoading(true);
          const res = await Axios.post("/api/delivery/check", {
            from,
            to,
            mode,
          });
          console.log("Price & Disctance", res.data);
          setDistance(res.data.distance);
          setPrice(res.data.price);
        } catch (err) {
          console.error("Price Error", err);
          setAlert("Oops! Couldn't calculate price", "error");
        }
      })();
    } else {
      console.log("Fill form...");
      cleanupMini();
    }
    mounted && setLoading(false);
    return () => {
      mounted = false;
    };
  }, [from, to, mode, open, setAlert]);

  const create = async () => {
    if (
      client &&
      from &&
      to &&
      mode &&
      pickUpNumber &&
      dropOffNumber &&
      price &&
      distance
    ) {
      try {
        await Axios.post("/api/admin/delivery", {
          client,
          from,
          to,
          mode,
          pickUpNumber,
          dropOffNumber,
          note,
          price,
          distance,
        });
        close();
      } catch (err) {
        console.log("Error...", err);
      }
    } else {
      console.log("Please fill in all fields");
    }
  };

  return (
    <React.Fragment>
      <Box px={1} />
      <Button color="secondary" onClick={() => setOpen(true)}>
        Create New
      </Button>
      <Dialog
        open={open}
        onClose={close}
        aria-labelledby="new-delivery"
        maxWidth="lg"
      >
        <DialogTitle id="new-delivery">New Delivery</DialogTitle>
        <DialogContent>
          <Box
            width={700}
            height={300}
            p={1}
            display="flex"
            flexDirection="row"
          >
            <Grid container spacing={3}>
              <Grid item xs={12} md={6}>
                <SearchUser
                  label="Client"
                  onChange={(e) => (e ? setClient(e._id) : setClient(null))}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <Select
                  fullWidth
                  variant="outlined"
                  label="Mode"
                  value={mode}
                  onChange={(e) => setMode(e.target.value)}
                >
                  {modes.map((value, index) => (
                    <MenuItem key={index} value={value}>
                      {value}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>
              <Grid item xs={12} md={6}>
                <GoogleMaps
                  label="Pickup Location"
                  onChange={(e) => setFrom(e)}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  id="pickupphone"
                  label="Pickup Contact"
                  value={pickUpNumber}
                  onChange={(event) => setPickUpNumber(event.target.value)}
                  variant="outlined"
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <GoogleMaps
                  label="Dropoff Location"
                  onChange={(e) => setTo(e)}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  id="dropoffphone"
                  label="Dropoff Contact"
                  value={dropOffNumber}
                  onChange={(event) => setDropOffNumber(event.target.value)}
                  variant="outlined"
                  fullWidth
                />
              </Grid>

              <Grid item xs={12}>
                <TextField
                  id="note"
                  label="Note"
                  variant="outlined"
                  fullWidth
                  value={note}
                  onChange={(event) => setNote(event.target.value)}
                />
              </Grid>
            </Grid>
            <Paper
              style={{ marginLeft: 15, height: "100%" }}
              variant="outlined"
            >
              <Box p={5} width={300} height={1}>
                <Typography variant="body1" color="initial" gutterBottom>
                  Distance
                </Typography>
                <Box display="flex" flexDirection="row" alignItems="center">
                  <Typography variant="h3" color="initial" gutterBottom>
                    {distance || "0 km"}
                  </Typography>
                </Box>
                <Divider style={{ marginBottom: 15 }} />
                <Typography variant="body1" color="initial" gutterBottom>
                  Price
                </Typography>
                <Typography variant="h3" color="initial" gutterBottom noWrap>
                  {priceFormatter(price)}
                </Typography>
              </Box>
            </Paper>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={create}
            color="primary"
            disabled={
              !(
                client &&
                from &&
                to &&
                mode &&
                pickUpNumber &&
                dropOffNumber &&
                price &&
                distance &&
                !loading
              )
            }
          >
            Create
          </Button>
          <Button onClick={close} color="default">
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
};

const Deliveries = (props) => {
  const [pending, setPending] = React.useState([]);
  const [processing, setProcessing] = React.useState([]);
  const [delivered, setDelivered] = React.useState([]);
  const [cancelled, setCancelled] = React.useState([]);
  const [display, setDisplay] = React.useState([]);
  const [tab, setTab] = React.useState(0);
  const [loading, setLoading] = React.useState(false);

  const { newOrder, clearNewOrder } = React.useContext(NotificationsContext);

  React.useEffect(() => {
    _getDeliveries();
    clearNewOrder();
    // eslint-disable-next-line
  }, []);

  React.useEffect(() => {
    if (newOrder) {
      _getDeliveries();
      setTimeout(clearNewOrder, 10000);
    }
  }, [newOrder, clearNewOrder]);

  const _getDeliveries = async () => {
    setLoading(true);
    try {
      const res = await Axios.get("/api/admin/deliveries");
      const deliveries = res.data;
      setPending(
        deliveries.filter((delivery) => delivery.status === "Pending")
      );
      setProcessing(
        deliveries.filter((delivery) => delivery.status === "Processing")
      );
      setCancelled(
        deliveries.filter((delivery) => delivery.status === "Cancelled")
      );
      setDelivered(
        deliveries.filter((delivery) => delivery.status === "Delivered")
      );
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };
  const adapt = (data) => {
    const displayRows = data.map((row) => {
      const { dateCreated, client, from, to, distance, mode, _id } = row;
      const pickUp = from && from.address;
      const dropOff = to && to.address;
      const { name } = client || { name: "" };
      return {
        dateCreated,
        client: name,
        pickUp,
        dropOff,
        distance,
        mode,
        _id,
      };
    });
    return displayRows;
  };
  React.useEffect(() => {
    switch (tab) {
      case 0:
        setDisplay(adapt(pending));
        break;
      case 1:
        setDisplay(adapt(processing));
        break;
      case 2:
        setDisplay(adapt(delivered));
        break;
      case 3:
        setDisplay(adapt(cancelled));
        break;
      default:
        break;
    }
  }, [tab, loading, pending, processing, delivered, cancelled]);

  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="initial" gutterBottom>
          Deliveries
        </Typography>
        <NewDelivery />
      </Box>
      <TabTheme
        aria="deliveries tab"
        tabs={["Pending", "Processing", "Delivered", "Cancelled"]}
        onChange={(value) => setTab(value)}
      />
      <MyVirtualTable
        rows={display}
        columns={columns}
        getRowId={(row) => row._id}
        columnExtensions={columnExtensions}
        formatters={[<DateFormatProvider for={["dateCreated"]} />]}
        onClick={(id) => {
          props.history.push(`/dashboard/delivery/${id}`);
        }}
        loading={loading}
      />
    </React.Fragment>
  );
};

export default Deliveries;
