import React from "react";
import Box from "@material-ui/core/Box";
import TabTheme from "../components/core/TabTheme";
import Typography from "@material-ui/core/Typography";
import MyVirtualTable from "../components/core/VirtualTableTheme";

import Axios from "axios";
import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  TextField,
} from "@material-ui/core";

const columns = [
  { name: "name", title: "Name" },
  { name: "phone", title: "Phone" },
  { name: "email", title: "Email" },
];

const columnExtensions = [
  { columnName: "name" },
  { columnName: "phone" },
  { columnName: "email" },
];

const NewUsers = (props) => {
  const [open, setOpen] = React.useState(false);

  const [name, setname] = React.useState("");
  const [email, setemail] = React.useState("");
  const [phone, setphone] = React.useState("");
  const [password, setpassword] = React.useState("");
  const [secret, setsecret] = React.useState("");

  const [loading, setLoading] = React.useState(false);

  const close = () => {
    setOpen(false);
  };
  const clear = () => {
    setname("");
    setphone("");
    setemail("");
    setpassword("");
    setsecret("");
  };

  const create = async () => {
    if (name && email && phone && password && secret) {
      setLoading(true);
      try {
        await Axios.post("/api/users", { name, email, phone, password });
        clear();
        close();
      } catch (err) {
        console.error(err);
      }
      setLoading(false);
    }
  };
  return (
    <React.Fragment>
      <Box px={1} />
      <Button color="secondary" onClick={() => setOpen(true)}>
        Create New
      </Button>
      <Dialog open={open} onClose={close}>
        <DialogTitle>New Client</DialogTitle>
        <DialogContent>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                label="Name"
                value={name}
                onChange={(e) => setname(e.target.value)}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                variant="outlined"
                label="Email"
                value={email}
                onChange={(e) => setemail(e.target.value)}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                variant="outlined"
                label="Phone"
                value={phone}
                onChange={(e) => setphone(e.target.value)}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                variant="outlined"
                label="Password"
                type="password"
                value={password}
                onChange={(e) => setpassword(e.target.value)}
                fullWidth
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <TextField
                variant="outlined"
                label="Secret"
                value={secret}
                onChange={(e) => setsecret(e.target.value)}
                fullWidth
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={create}
            color="primary"
            disabled={
              loading || !(name && email && phone && password && secret)
            }
          >
            Create
          </Button>
          <Button onClick={close} color="default">
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
};

const Users = (props) => {
  React.useEffect(() => {
    _getDrivers();
  }, []);
  const [pending, setPending] = React.useState([]);
  const [active, setActive] = React.useState([]);
  const [blacklist, setBlacklist] = React.useState([]);
  const [display, setDisplay] = React.useState([]);
  const [tab, setTab] = React.useState(0);
  const [loading, setLoading] = React.useState(false);

  const _getDrivers = async () => {
    setLoading(true);
    try {
      const res = await Axios.get("/api/admin/users");

      const users = res.data;
      setPending(users.filter((user) => !user.valid && !user.banned));
      setActive(users.filter((user) => user.valid && !user.banned));
      setBlacklist(users.filter((user) => user.banned));
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  React.useEffect(() => {
    switch (tab) {
      case 0:
        setDisplay(active);
        break;
      case 1:
        setDisplay(pending);
        break;
      case 2:
        setDisplay(blacklist);
        break;
      default:
        break;
    }
  }, [tab, loading, pending, active, blacklist]);

  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="initial" gutterBottom>
          Users
        </Typography>
        <NewUsers />
      </Box>
      <TabTheme
        aria="deliveries tab"
        tabs={["Active", "Pending", "Blacklist"]}
        onChange={(value) => setTab(value)}
      />
      <MyVirtualTable
        rows={display}
        columns={columns}
        getRowId={(row) => row._id}
        columnExtensions={columnExtensions}
        loading={loading}
        // onClick={(id) => {
        //   props.history.push(`/dashboard/driver/${id}`);
        // }}
      />
    </React.Fragment>
  );
};

export default Users;
