import React from "react";
import Box from "@material-ui/core/Box";
import TabTheme from "../components/core/TabTheme";
import Typography from "@material-ui/core/Typography";
import MyVirtualTable from "../components/core/VirtualTableTheme";

import Avatar from "@material-ui/core/Avatar";

import { DataTypeProvider } from "@devexpress/dx-react-grid";
import Axios from "axios";
import DriversContext from "../components/context/drivers/DriversContext";
import DriverMarkers from "../components/core/DriverMarkers";
import GoogleMapReact from "google-map-react";
import {
  Button,
  DialogActions,
  DialogContent,
  DialogTitle,
  Dialog,
  Grid,
  TextField,
  Paper,
} from "@material-ui/core";

const columns = [
  { name: "photoUrl", title: " " },
  { name: "name", title: "Name" },
  { name: "staffID", title: "Staff ID" },
  { name: "phone", title: "Phone" },
  { name: "email", title: "Email" },
];

const columnExtensions = [
  { columnName: "photoUrl", width: 100, align: "center" },
  { columnName: "name" },
  { columnName: "staffID" },
  { columnName: "phone" },
  { columnName: "email" },
];

const AvatarFormatter = ({ value }) => (
  <Avatar alt="Driver image" src={value} />
);

const AvatarProvider = (props) => (
  <DataTypeProvider formatterComponent={AvatarFormatter} {...props} />
);

const NewDriver = () => {
  const [open, setOpen] = React.useState(false);
  const [photoUrl, setPhotoUrl] = React.useState("");
  const [name, setname] = React.useState("");
  const [email, setemail] = React.useState("");
  const [phone, setphone] = React.useState("");
  const [staffID, setstaffID] = React.useState("");
  const [password, setpassword] = React.useState("");
  const [secret, setsecret] = React.useState("");

  const [error, setError] = React.useState(false);
  const [loading, setLoading] = React.useState(false);

  const close = () => {
    setOpen(false);
  };
  const clear = () => {
    setOpen("");
    setPhotoUrl("");
    setname("");
    setemail("");
    setphone("");
    setstaffID("");
    setpassword("");
    setsecret("");
  };

  const create = async () => {
    if (
      !error &&
      photoUrl &&
      password &&
      name &&
      secret &&
      phone &&
      staffID &&
      email
    ) {
      try {
        setLoading(true);
        await Axios.post("/api/drivers", {
          photoUrl,
          password,
          name,
          secret,
          phone,
          staffID,
          email,
        });
        clear();
        close();
      } catch (err) {
        console.error("Create Error...", err);
      }
      setLoading(false);
    }
  };

  return (
    <React.Fragment>
      <Box px={1}>
        <Button color="secondary" onClick={() => setOpen(true)}>
          Create New
        </Button>
        <Dialog
          open={open}
          onClose={close}
          aria-labelledby="new-driver"
          maxWidth="lg"
        >
          <DialogTitle id="new-driver">New Driver</DialogTitle>
          <DialogContent>
            <Box
              width={700}
              height={300}
              p={1}
              display="flex"
              flexDirection="row"
            >
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <TextField
                    id="driver-name"
                    label="Name"
                    variant="outlined"
                    fullWidth
                    value={name}
                    onChange={(e) => setname(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    id="driver-email"
                    label="Email"
                    variant="outlined"
                    fullWidth
                    value={email}
                    onChange={(e) => setemail(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    id="driver-phone"
                    label="Phone"
                    variant="outlined"
                    fullWidth
                    value={phone}
                    onChange={(e) => setphone(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    id="driver-id"
                    label="StaffID"
                    variant="outlined"
                    fullWidth
                    value={staffID}
                    onChange={(e) => setstaffID(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    id="driver-password"
                    label="Password"
                    variant="outlined"
                    type="password"
                    fullWidth
                    value={password}
                    onChange={(e) => setpassword(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    id="secret"
                    label="Secret"
                    variant="outlined"
                    type="password"
                    fullWidth
                    value={secret}
                    onChange={(e) => setsecret(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Paper
                style={{ marginLeft: 15, height: "100%" }}
                variant="outlined"
              >
                <Box p={5} width={300} height={1}>
                  {!error || !photoUrl ? (
                    <img
                      onError={() => setError(true)}
                      onLoad={() => {
                        setError(false);
                      }}
                      style={{
                        borderRadius: 75,
                      }}
                      mode="cover"
                      height={150}
                      width={150}
                      src={
                        photoUrl ||
                        "https://www.flaticon.com/svg/vstatic/svg/727/727399.svg?token=exp=1614329769~hmac=e631322c93b0fb47d1138094522c2b5a"
                      }
                      alt="Driver"
                    />
                  ) : (
                    <img
                      style={{
                        borderRadius: 75,
                      }}
                      mode="cover"
                      height={150}
                      width={150}
                      src="https://www.flaticon.com/svg/vstatic/svg/1481/1481932.svg?token=exp=1614329867~hmac=6a1aaf9b6469a7538a88cd67c08ed57d"
                      alt="Feedback"
                    />
                  )}

                  <TextField
                    id="photoUrl"
                    label="Profile photo URL"
                    variant="standard"
                    value={photoUrl}
                    onChange={(e) => setPhotoUrl(e.target.value)}
                    fullWidth
                  />
                </Box>
              </Paper>
            </Box>
          </DialogContent>
          <DialogActions>
            <Button
              color="primary"
              disabled={
                error ||
                loading ||
                !(
                  photoUrl &&
                  password &&
                  name &&
                  secret &&
                  phone &&
                  staffID &&
                  email
                )
              }
              onClick={create}
            >
              Create
            </Button>
            <Button onClick={close} color="default">
              Cancel
            </Button>
          </DialogActions>
        </Dialog>
      </Box>
    </React.Fragment>
  );
};

const Drivers = (props) => {
  React.useEffect(() => {
    _getDrivers();
  }, []);
  const [pending, setPending] = React.useState([]);
  const [active, setActive] = React.useState([]);
  const [blacklist, setBlacklist] = React.useState([]);
  const [display, setDisplay] = React.useState([]);
  const [tab, setTab] = React.useState(0);
  const [loading, setLoading] = React.useState(false);
  const { markers } = React.useContext(DriversContext);
  const _getDrivers = async () => {
    setLoading(true);
    try {
      const res = await Axios.get("/api/admin/drivers");

      const drivers = res.data;
      setPending(drivers.filter((driver) => !driver.valid && !driver.banned));
      setActive(drivers.filter((driver) => driver.valid && !driver.banned));
      setBlacklist(drivers.filter((driver) => driver.banned));
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  React.useEffect(() => {
    switch (tab) {
      case 0:
        setDisplay(active);
        break;
      case 1:
        setDisplay(pending);
        break;
      case 2:
        setDisplay(blacklist);
        break;
      default:
        break;
    }
  }, [tab, loading, pending, active, blacklist]);

  const body = (
    <MyVirtualTable
      rows={display}
      columns={columns}
      getRowId={(row) => row._id}
      formatters={[<AvatarProvider for={["photoUrl"]} />]}
      columnExtensions={columnExtensions}
      loading={loading}
      onClick={(id) => {
        props.history.push(`/dashboard/driver/${id}`);
      }}
    />
  );

  const map = (
    <Box width="100%" style={{ height: 500 }}>
      <GoogleMapReact
        bootstrapURLKeys={{
          key: "AIzaSyAVa67y3wWEBxeAR5Q0p4PZtVknjarOlhQ",
        }}
        defaultCenter={{ lat: 4.8156, lng: 7.0498 }}
        defaultZoom={15}
      >
        {markers.map((driver) => (
          <DriverMarkers
            lat={driver.latitude}
            lng={driver.longitude}
            src={driver.photoUrl}
            name={driver.name}
            id={driver._id}
            action="View"
            to={`/dashboard/driver/${driver._id}`}
            animate
          />
        ))}
      </GoogleMapReact>
    </Box>
  );

  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="initial" gutterBottom>
          Drivers
        </Typography>
        <NewDriver />
      </Box>
      <TabTheme
        aria="deliveries tab"
        tabs={["Active", "Pending", "Blacklist", "Map"]}
        onChange={(value) => setTab(value)}
      />
      {tab === 3 ? map : body}
    </React.Fragment>
  );
};

export default Drivers;
